import React from "react";

const Card = ({ title, image }) => {
  return (
    <div className="w-full max-w-xs flex flex-col items-center p-4 bg-white bg-opacity-25 rounded-3xl">
      <img className="w-full aspect-video" src={image} alt="" />
      <h1 className="p-4 text-3xl text-center font-medium">{title}</h1>
    </div>
  );
};

export default Card;
