import React from "react";

const Footer = () => {
  return (
    <footer className="p-16 bg-black text-white grid grid-cols-2">
      {/* About */}
      <div>
        <h2 className="text-6xl font-extrabold opacity-50 lowercase">
          About Us
        </h2>
        <p className="w-4/5 text-2xl py-16 opacity-75">
          Students fo MIT working on their Startup. Group of highly motivated
          individuals building a habit bulding community of different
          categories.
        </p>
      </div>

      {/* External Links */}
      <div>
        <h2 className="text-6xl font-extrabold opacity-50 lowercase">
          Reach Us
        </h2>
        <div className="flex gap-16 text-5xl py-16 opacity-75">
          <i class="fab fa-instagram"></i>
          <i class="fab fa-discord"></i>
          <i class="fab fa-twitter"></i>
          <i class="fas fa-envelope"></i>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
